    <meta
      charset="utf-8"
      name="viewport"
      content="width=device-width, initial-scale=1.0"
    />
    <div class="slide">
      <div class="ad">
        <img src="../../assets/adtop.jpg" />
        <div class="ad_text">時時刻刻與你歡聚每一刻</div>
      </div>

      <div class="product_top">
        <p>品牌介紹</p>
      </div>
      <div class="wrap">
        <div class="ad1">
          <div class="ad2" >
            <img src="../../assets/Orion7.jpg" alt="" />
            <div class="img_content radius">
              <div>
                <img src="../../assets/orion.jpg" alt="" />
                <p>
                  沖繩的代表性啤酒「Orion
                  Beer」選用沖繩在地伊江島產大麥、稻米及山原淨水，以更長的熟成時間帶出馥郁口感！
                </p>
              </div>
            </div>
          </div>

          <div class="ad2" >
            <img src="../../assets/Asahi8.jpg" alt="" />

            <div class="img_content radius">
              <div>
                <img src="../../assets/Asahi.png" alt="" />
                <p>
                  Asahi Super
                  Dry是款優質啤酒。乾爽清脆，味道無甜，恬淡。清澈的淺金色啤酒，泡沫細緻綿密，清甜檸檬草本香氣與清爽口感。
                </p>
              </div>
            </div>
          </div>

          <div class="ad2">
            <img src="../../assets/estrella_damm.png" alt="" />
            <div class="img_content radius">
              <div>
                <img src="../../assets/ESTRELLA.jpg" alt="" />
                <p>
                  由100%純天然地中海原料釀造，麥芽、大米和啤酒花的配方使其口感清香順滑。
                </p>
              </div>
            </div>
          </div>

          <div class="ad2" >
            <img src="../../assets/corona.jpg" alt="" />
            <div class="img_content radius">
              <div>
                <img src="../../assets/corona_logo.jpg" alt="" />
                <p>
                  源自墨西哥的啤酒領導品牌頂級啤酒花，優質大麥，純淨水源及酵母釀製而成屬Pilsener
                  式窖藏啤酒，品質是其一貫的堅持。
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <ccc/>
      <slogan/>
      <!-- <div class="swiperclass">
        <HomeSwiper/>
      </div> -->


      


      <!-- <img src="../../assets/ad2.jpg" alt=""> -->
    </div >
       <!-- <swiper2/> -->
  </div>
</template>

    


<script>
import $ from "jquery";
// import HomeSwiper from "./HomeSwiper";
// import swiper2 from "./swiper2";
import ccc from "./ccc"
import slogan from "./slogan"
export default {
  components: {
    // HomeSwiper,
    ccc,
    slogan,
    // swiper2,
  },
  data() {
    return {
      isopacity: false,
    };
  },
  methods: {
   

    hover_in(e) {
      $(e.path[1].children[1]).css("opacity", 1);
    },
    hover_out(e) {
      $(e.path[1].children[1]).css("opacity", 0.9);
    },
  },
  created() {
    
  },
};
</script>



<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+HK:wght@700&display=swap");
.swiperclass{
  width: 100%;
  height: auto;
  /* padding: 10px; */
  z-index: 9;
  bottom: 300px;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 100px;
}
.slide {
  width: 100%;
  position: absolute;
  top: 0;
  background-color: #f7f7f7;
}
img {
  width: 100%;
  /* height: 360px; */
  position: relative;
}
.ad {
  /* background-image: url("../../assets/adtop.jpg"); */
  /* background-image: url(https://images.unsplash.com/photo-1546622891-02c72c1537b6?ixid=MnwxMjA3fDB8MHxzZWFyY2h8MzR8fGJlZXJ8ZW58MHx8MHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1200&q=60); */
  background-size: cover;
  background-attachment: fixed;
  height: 800px;
  /* height: 55vh; */
  /* height: auto; */
  /* min-height: 35rem; */
  background-position: 50%;
  overflow: hidden;
  position: relative;
}
.ad_text {
  color: white;
  font-size: 30px;
  position: absolute;
  top: 60%;
  left: 70%;
  /* background-color: black; */
  padding: 10px;
  letter-spacing: 3px;
  font-weight: 600px;
}
.ad2 {
  width: 600px;
  display: flex;
  margin: 10px;
}
.ad2 > img {
  width: 100%;
  height: 360px;
  left: -100px;
}
.img_content {
  width: 200px;
  height: 100%;
  background-color: white;
  opacity: 1;
  position: absolute;
  right: 20px;
  padding: 20px;
  /* margin-top: 20px;
  margin-bottom: 20px; */
}
.img_content p {
  font-size: 14px;
  width: 100px;
  display: inline;
  line-height: 1.8 !important;
}
.ad1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.ad1 p {
  width: 500px;
  margin: 0 auto;
  line-height: 32px;
}
.product_top {
  margin: 0 auto;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: solid 1px #ccc;
  margin-bottom: 20px;
}
.product_top p {
  color: #014293;
  font-size: 42px;
  font-weight: 500;
  font-family: "Noto Sans HK", sans-serif;
  padding: 10px;
  padding-top: 30px;
}
.ad2 {
  overflow: hidden;
  position: relative;
}
.ad2 > img:hover {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}
/* .ad2 img:hover{
  opacity: 1;
} */

.radius {
  position: absolute;
  right: 0;
  width: 50%;
}
/* .radius > div {
  display: flex;
  flex-direction: column;
  align-items: center;
} */
.radius img {
  /* position: absolute; */
  right: 0;
  /* width: 50%; */
}
.radius p {
  /* font-size: 10px; */
  width: 100%;
}

#app {
  height: 100%;
}
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 560px) {
  .ad{
    height: auto !important;
  }
  .ad > .ad_text {
    display: none;
  }
  .ad2 {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .ad2 > img {
    overflow: hidden;
    background-position: center;
    left: 0;
  }
  .product_top p {
    font-size: 26px;
  }
  .radius {
    /* position: absolute; */
    right: 0;
    width: 100%;
  }
  .radius > div {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .radius img {
    /* position: absolute; */
    /* right: 0; */
    /* left:100px; */
    width: 50%;
    margin-top: -15px;

  }
  .radius p {
    /* font-size: 10px; */
    width: 70%;
  }
  .img_content {
    opacity: 0.8;
  }
}
</style>


