<template>
  <div>
      <meta
      charset="utf-8"
      name="viewport"
      content="width=device-width, initial-scale=1.0"
    />
    <header class="navbar navbar-dark sticky-top flex-md-nowrap p-0">
      <input type="checkbox" id="menu_control" />

      <a class="navbar-brand col-md-3 col-lg-2 me-0 px-3" href="#"
        ><img
          class="beermd"
          src="../assets/beermd_yellow.png"
          alt=""
          style="width: 150px"
      /></a>


      <label for="" class="menu_btn" @click="menuisshow = !menuisshow">
        <span>menu</span>
      </label>

       

      <div class="header_nav" :class="{showmenu : menuisshow}">
        <div class="nav-item text-nowrap">
          <a class="nav-link px-3" href="#" @click="menuisshow = !menuisshow">
            <router-link to="/"> 首頁 </router-link></a
          >
        </div>

        <div class="nav-item text-nowrap">
          <a class="nav-link px-3" href="#" @click="menuisshow = !menuisshow">
            <router-link to="/customerorder"> 產品列表 </router-link></a
          >
        </div>

        <div class="nav-item text-nowrap">
          <a class="nav-link px-3" href="#" @click="menuisshow = !menuisshow"
            ><router-link to="/admin/product">登入</router-link></a
          >
        </div>

        <div class="nav-item text-nowrap">
          <a
            class="car nav-link px-3"
            href="#"
            @click.prevent="isshow = !isshow , menuisshow = false"
            @click="getfinalCar"
          >
            <a>購物車</a></a
          >
        </div>
      </div>
     <div
        class="mask"
        :class="{ open: menuisshow}"
        @click="menuisshow = !menuisshow"
      ></div>

      

      

      <div class="navbar-nav">
        <div class="nav-item text-nowrap">
          <a class="nav-link px-3" href="#">
            <router-link to="/"> 首頁 </router-link></a
          >
        </div>

        <div class="nav-item text-nowrap">
          <a class="nav-link px-3" href="#">
            <router-link to="/customerorder"> 產品列表 </router-link></a
          >
        </div>

        <div class="nav-item text-nowrap">
          <a class="nav-link px-3" href="#"
            ><router-link to="/admin/product">登入</router-link></a
          >
        </div>

        <div class="nav-item text-nowrap">
          <a
            class="car nav-link px-3"
            href="#"
            @click.prevent="isshow = !isshow"
            @click="getfinalCar"
          >
            <a>購物車</a></a
          >
        </div>
      </div>

      <div class="top" :class="{ show: isshow }">
        <loading :active.sync="isLoading"></loading>
        <table>
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id" class="cartItem">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click.prevent="removecartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                <img
                  :src="item.product.imageUrl"
                  alt=""
                  style="width: 100px"
                /><br />

                {{ item.product.title }}
                <!-- <div class="text-success" v-if="item.coupon">
          已套用優惠券
        </div> -->
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}</td>
            </tr>
            <tr>
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" placeholder="請輸入優惠碼" />
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button">
              套用優惠碼
            </button>
          </div>
        </div>
        <div>
          <button
            class="topay btn btn-success"
            @click.prevent="goOrder"
            @click="isshow = !isshow"
          >
            結帳
          </button>
        </div>
      </div>
      <div
        class="mask"
        :class="{ open: isshow }"
        @click="isshow = !isshow"
      ></div>
    </header>
  </div>
</template>


<script>
import $ from "jquery";
export default {
  data() {
    return {
      cart: {},
      isshow: false,
      isLoading: false,
      menuisshow:false,
    };
  },
  methods: {
    navbar_show() {
      $(document).ready(function () {
        $(window).scroll(function () {
          var body_height = $("body").height();
          console.log(body_height);
          var sum = 16;
          console.log($(window).scrollTop());

          if ($(window).scrollTop() >= sum) {
            $("header")
              .css("background-color", "#fff")
              .css("position", "fixed");
            $("header a").css("color", "black");
            $("header").css("transition", "0.5s ease");
          } else {
            $("header")
              .css("background-color", "transparent")
              .css("position", "fixed");
            $("header a").css("color", "#fff");
            $("header").css("transition", "0.5s ease");
          }

          // if($(window).scrollTop()>=goal){
          //    $('header').css("display",'block')
          // }
        });
      });
    },
    singout() {
      const api = `${process.env.APIPATH}/logout`;
      const vm = this;
      this.$http.post(api).then((response) => {
        if (response.data.success) {
          vm.$router.push("/login");
        }
      });
    },

    goProducts() {
      vm.$router.push("/");
    },

    getfinalCar() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.cart = response.data.data;
        console.log(vm.cart);
        vm.isLoading = false;
      });
    },
    removecartItem(id) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(api).then((response) => {
        // console.log(vm.cart);
        this.getfinalCar();
        vm.isLoading = false;
      });
    },
    goBack() {
      const vm = this;
      vm.$router.push("/customer_order");
    },
    getCar() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        console.log(response);
        this.getfinalCar();
        vm.isLoading = false;
      });
    },
    goOrder() {
      const vm = this;
      vm.$router.push("/order");
    },
    closemenu(){
      $('.header_nav').css('left','-100%')
    }
 

  },
  created() {
    this.getCar();
    this.getfinalCar();
    this.navbar_show();
  },
};
</script>

<style>
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+HK&display=swap");
* {
  box-sizing: border-box;
}
.nav{
  background-color: #0a3e8a;
}
header {
  background-color: none;
  font-family: "Noto Sans HK", sans-serif;
  position: fixed;
  height: 100px;
  width: 100%;
}
.navbar-nav {
  flex-direction: row;
  
}
.navbar-brand {
  color: black !important;
  
}

.top {
  width: 800px;
  position: fixed;
  right: -800px;
  top: 0;
  bottom: 0;
  padding: 10px;
  background: #fff;
  z-index: 2002;
  overflow: scroll;
  transition: 0.5s ease-in-out;
  padding: 30px;
}
table {
  width: 100%;
}
.show {
  right: 0;
}
.car :hover {
  color: #337ab7 !important;
}
.cartItem {
  border-bottom: solid 1px grey;
}
.mask {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1999;
}
.open {
  display: block;
}

.topay {
  width: 100%;
}
router-link {
  list-style: none;
}
.navbar-nav {
  margin-right: 10px;
}
.nav-link {
  color: white !important;
}
.nav-link :hover {
  color: #337ab7 !important;
}

.nav-item a {
  color: white;
  text-decoration: none;
  font-size: 18x;
}

.nav-item :hover {
  color: #337ab7;
  text-decoration: none;
}
.login {
  display: none;
}

#menu_control {
  display: none;
}
.menu_btn span {
  opacity: 0;
}
.header_nav {
  display: none;
}
.menumask{
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1999;
  }


@media (max-width: 1200px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .navbar-nav {
    display: none;
  }
  .menu_btn {
    border: 2px solid #ccc;
    width: 40px;
    height: 40px;
    background-color: transparent;
    position: absolute;
    right: 10px;
    border-radius: 5px;
    z-index: 2001;
  }

  .menu_btn::before {
    content: "";
    position: absolute;
    height: 2px;
    left: 2px;
    right: 2px;
    /* width: 36px; */
    background-color: #ccc;
    top: 0;
    bottom: 0;
    margin: auto;
    box-shadow: 0px 8px 0px #ccc, 0px -8px 0px #ccc;
  }
  .header_nav {
    right: -100%;
    /* top:-100%; */
    display: block;
    width: 80%;
    margin-top: 100px;
    /* padding-top: 300px; */
    height: 100vh;
    /* height: calc(100vh - 50px); */
    position: absolute;
    top: 0px;
    background-color: #fff;
    transition: ease-in-out 0.3s;
    z-index: 2000;
    padding: 20px;
  }
  #menu_control:checked ~ .header_nav {
    left: 0;
    /* top:100vh; */
  }
  .header_nav div {
    border-bottom: 1px solid #ccc;
  }
  .nav-item a{
    color: cadetblue !important;
  }

  /* 購物車 */
  .top {
    width: 90%;
    position: fixed;
    right: -800px;
    top: 0;
    bottom: 0;
    padding: 10px;
    background: #fff;
    z-index: 2002;
    overflow: scroll;
    transition: 0.5s ease-in-out;
    padding: 30px;
  }
  .show {
    right: 0;
  }
  .align-middle {
    font-size: 16px;
  }
  .showmenu{
    right: 0;
  }
  .ad2 >img{
    height: auto !important;
  }



  
}
</style>







